@import url('https://fonts.googleapis.com/css2?family=Playfair+Display&family=Poppins:wght@200;400;700&display=swap');

body {
    margin: 0;
    height: 100vh;
    font-family: 'Poppins';
}
.wrapper {
    overflow-x: hidden;
    // position: relative;
}
* {
    box-sizing: border-box;
}


/* Header & Navigation */




.header {
    display: flex;
    width: 100%;
    padding: 1.5rem 10%;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    background-color: #fff;
    box-shadow: 4px 4px 22px 0px rgba(100, 19, 231, 0.137);
    z-index: 100;

}

nav {
    position: relative;
}

nav ul {
    list-style-type: none;
    margin: 0;
    display: flex;
    gap: 3.5em;
    position: relative;
}

nav ul li a {
    text-decoration: none;
    color: black;
    text-transform: uppercase;
    position: relative;
    font-size: 1rem;
}

nav ul li a.active:before {
    position: relative;
    content: "";
    width: 35%;
    height: 1px;
    border-bottom: 2px solid black;
    bottom: -6px;
}

nav ul li a:before {
    position: relative;
    content: "";
    width: 0%;
    height: 1px;
    border-bottom: 3px solid blueviolet;
    bottom: -6px;
    border-radius: 2rem;
    transition: width .10s;
}

nav ul li a:hover:before {
    width: 80%;
}

