// //Text Animation


.text-anim {
    height: 100vh;
    padding: 0 clamp(4rem, 12vw, 20rem);
    display: grid;
    place-content: center;

    p {
        font-size: clamp(2rem, 5vw, 8rem);
    }

    &:nth-of-type(1) {
        border-bottom: 1px solid black;
    }

    &:nth-of-type(3) {
        background: #ffff00;
    }

    &:nth-of-type(4) {
        background: rgb(29, 29, 29);
        color: white;
    }
}

//Image Galery


.big-text { 
    font-size: 14rem; 
    font-family: 'Bebas Neue';
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    margin: 0;
    color: white;
}
.foto {
    display: inline-flex;
    grid-template-columns: repeat(3, 1fr);
    position: absolute;
    z-index: -2;
}

//image bergerak

.fixed-image{
    margin-right: 20rem;
    margin-top: 5rem;
  
    .inner {
      width: 36%;
      max-width: 1200px;
      margin: auto;
      position: relative;
    }
  
    .image {
      width: 100%;
      overflow: hidden;
      position: sticky;
      top: 120px;
      left: 50%;
  
      img {
        width: 10rem;
        height: auto;
        width: 100%;
        object-fit: cover;
        object-position: center center;
      }
    }
  
    .content {
      display: flex;
      padding-top: 40%;
      color: #fff;
      font-size: 24px;
  
      p {
        width: 50%;
        margin-left: auto;
      }
    }
  }

  a{
text-decoration:none;
outline:none !important;
color:#fff;
}

.border-shape {
background: #ffbe00 none repeat scroll 0 0;
color: #fff;
display: block;
height: 3px;
left: 0;
margin: 20px auto;
position: relative;
right: 0;
text-align: center;
top: 0;
width: 80px;
}

.border-shape::before {
background: #ffbe00 none repeat scroll 0 0;
bottom: 0;
content: "";
height: 1px;
left: 80px;
margin: 0 auto;
position: absolute;
text-align: center;
top: 1px;
width: 100px;
}

.border-shape::after {
background: #ffbe00 none repeat scroll 0 0;
bottom: 0;
content: "";
height: 1px;
margin: 0 auto;
position: absolute;
right: 80px;
text-align: center;
top: 1px;
width: 100px;
}














